.m-spinner {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: #fff;
  pointer-events: none;
}

.m-spinner > div {
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  width: 50px;
  height: 50px;
  opacity: 0.5;
  border-radius: 100%;
  background-color: #59b6d7;
  -webkit-animation: scale1 2s infinite ease-in-out;
  animation: scale1 2s infinite ease-in-out;
}

